import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root',
})
export class NewsapiservicesService {
  constructor(private _http: HttpClient) {}

  newApiUrl =
    'https://newsapi.org/v2/top-headlines?country=il&apiKey=8704df4a09c9466b9dc1e66ee77720ec';

  techApiUrl =
    'https://newsapi.org/v2/top-headlines?country=il&category=technology&apiKey=8704df4a09c9466b9dc1e66ee77720ec';

  sportApiUrl =
    'https://newsapi.org/v2/top-headlines?country=il&category=sports&apiKey=8704df4a09c9466b9dc1e66ee77720ec';

  businessApiUrl =
    'https://newsapi.org/v2/top-headlines?country=il&category=business&apiKey=8704df4a09c9466b9dc1e66ee77720ec';

  entertainmentApiUrl =
    'https://newsapi.org/v2/top-headlines?country=il&category=entertainment&apiKey=8704df4a09c9466b9dc1e66ee77720ec';

  healthApiUrl =
    'https://newsapi.org/v2/top-headlines?country=il&category=health&apiKey=8704df4a09c9466b9dc1e66ee77720ec';

  topHeading(): Observable<any> {
    return this._http.get(this.newApiUrl);
  }

  techNews(): Observable<any> {
    return this._http.get(this.techApiUrl);
  }

  sportsNews(): Observable<any> {
    return this._http.get(this.sportApiUrl);
  }

  businessNews(): Observable<any> {
    return this._http.get(this.businessApiUrl);
  }

  entertainmentNews(): Observable<any> {
    return this._http.get(this.entertainmentApiUrl);
  }

  healthNews(): Observable<any> {
    return this._http.get(this.healthApiUrl);
  }
}
